require('./dashboard.scss');

var $ = require("jquery");

var React = require("react");
var ReactDOM = require("react-dom");

var AddProject = React.createClass({
  getInitialState: function() {
    return { showDiv: "inherit" }
  },
  componentDidMount: function() {
    var self = this;
    document.addEventListener("addProject", function(e) {
      self.showDiv();
    }, false);
  },
  showDiv: function() {
    this.setState({ display: "block"});
  },
  addProject: function(){
    var self = this;
    var date_added = new Date();
    //change date to django format
    date_added = date_added.toISOString();

    var sendData = { name: $("#createProjectName").val(), description: $("#createProjectDescription").val(), date_added: date_added}
    $.ajax({
      url: "http://127.0.0.1:8000/project/projects/",
      dataType: "json",
      method: "POST",
      data: sendData,
      success: function(data) {
        var id = data.id
      }.bind(this),
      error: function(xhr, status, err) {
          console.log("localhost:8000/project/project", status, err.toString());
      }.bind(this)
    });
  },
  render: function() {
    var style = { display: this.state.showDiv};
    var dashboard = <div>
                            <div className="dashboard-context-menu">
                              <div id="tabs"></div>
                            </div>

                         </div>
    return (
      <div>
        <Dashboard style={style} body={dashboard} />
      </div>
    )
  }
});

var Dashboard = React.createClass({
  render: function () {
    return (
      <div id="addProject" style={this.props.style}>
        {this.props.body}
      </div>
    )
  }
});

ReactDOM.render(
  <AddProject />,
  document.getElementById("add-project")
)

